.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  
  .box {
   
    padding: 20px;
    font-size: 1.0em;
    

  }

  
  .box1{
    grid-row: span 2;
    
  }
  .box2 {
    grid-row: span 2;
    
  }
  
  .box3 {
    grid-column: span 2;
    text-align: center;
    
  }

  h3 {
    text-align: center;
  }
  
  li {
    margin-top: 100px;
    margin-bottom: 100px;

  }
  img {
    padding-left: 500px;
    width: 1200px;
  }