
body {
    margin-left: 700px;
    margin-right: 700px;
    background-color: black;
    color: white;
  }
header {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
 
}
.container1 {
  display: flex;
  justify-content: center;
  color: white;
  border:10px solid #ffa500;
  border-width: 10px;
}
.container2 {
  outline: dashed 1px black;
  border:10px solid orange;
  border-width: 10px;
}
nav {
  margin-left: 100px;
  font-size: 50px;
  white-space: nowrap;
  text-align: center;
}
main{
  margin-left: 20px;
  font-size: 100px;
  
  
}
a:link, a:visited {
  padding: 100px;
  text-decoration: none;
  display: inline-block;
  width:600px;
  font-size: 1.17em;
  border: 1px solid orange ;
  font-weight: bold;
  color: #eeeeee;
}
a:hover, a:active { 
  background-color: #FF4500; 
  color:white ;
}
.find-btn{
	text-align: center;
  
}
button {
  padding: 100px;
  text-decoration: none;
  width:600px;
  font-size: 1em;
  border-left: 10px solid orange; 
  border-width: 10px;
  font-weight: bold;
  display: block;
  margin: 0 auto;
  background-color: black;
  color: white;
  border: 1px solid orange ;
  font-weight: bold;
  color: #eeeeee;
}
button:hover {
  background-color: #FF4500;
 
}
.name {
  font-weight: bold; 
  margin-right: 20px;
  font-size: 50px;
  white-space: nowrap;
}
